<template>
    <div style="background-image: url('/bg5.png');background-size: 100% 100%;width: 100%;overflow: hidden;">
        <el-card style="width: 80%;margin: 20px 10% 20px 10%">
            <!---->
            <div class="m-content">
                <!---->
                <el-form ref="passage" :model="passage" :rules="passageRules" class="demo-passage" label-position="top"
                         label-width="100px">
                    <!---->
                    <el-row :gutter="20">
                        <el-col :span="12">
                            <el-form-item label="" prop="title">
                                <el-input v-model="passage.title" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <!---->
                    <el-row :gutter="20">
                        <el-col :span="12">
                            <el-form-item label="" prop="description">
                                <el-input v-model="passage.description" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <!---->
                    <el-row :gutter="20">
                        <el-col :span="12">
                            <el-form-item label="" prop="type">
                                <el-input v-model="passage.type" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-form-item label="" prop="content">
                        <mavon-editor v-model="passage.content"></mavon-editor>
                    </el-form-item>
                    <!---->
                    <el-form-item>
                        <el-button type="primary" @click="submitForm"></el-button>
                        <el-button @click="resetForm"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-card>
    </div>
</template>

<script>
import passage from "@/components/Passage.vue";

export default {
    name: "EditPassage",
    props: ['id'],
    data() {
        return {
            passage: {},
            resetPassage: {},
            passageRules: {
                title: [
                    {required: true, message: '', trigger: 'blur'},
                    {min: 2, max: 45, message: ' 3  45 ', trigger: 'blur'}
                ],
                description: [
                    {required: true, message: '', trigger: 'blur'},
                    {max: 200, message: '200', trigger: 'blur'}
                ],
                content: [
                    {required: true, message: '', trigger: 'blur'}
                ],
                type: [
                    {required: true, message: '', trigger: 'blur'},
                    {max: 20, message: '20', trigger: 'blur'}
                ]
            }
        }
    },
    methods: {
        submitForm() {
            this.$refs['passage'].validate((valid) => {
                if (valid) {
                    this.$http.post("/passage/", this.passage).then((response) => {
                        if (response.data.error !== null){
                            this.$message.error(response.data.msg)
                            return
                        }
                        this.$message.success("!")
                    })
                } else {
                    console.log("error")
                }
            })
        },
        resetForm() {
            console.log(this.resetPassage)
            //
            this.passage = JSON.parse(JSON.stringify(this.resetPassage))
        }

    },
    created() {
        this.$http.get("/passage/" + this.id).then((response) => {
            if (response.data.error !== null){
                this.$message.error(response.data.msg)
            }else {
                this.passage = response.data.data
                //
                this.resetPassage = JSON.parse(JSON.stringify(response.data.data))
            }
        })
    },

}
</script>

<style scoped>
.m-content {
    width: 100%;
}
</style>