<template>
    <div style="background-image: url('/bg5.png');background-size: 100% 100%;width: 100%;overflow: hidden;">
        <el-card style="width: 80%;margin: 20px 10% 20px 10%">
            <!---->
            <div class="m-content">
                <!---->
                <el-form ref="passage" :model="passage" :rules="passageRules" class="demo-passage" label-position="top"
                         label-width="100px">
                    <!---->
                    <el-row :gutter="20">
                        <el-col :span="12">
                            <el-form-item label="" prop="title">
                                <el-input v-model="passage.title" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <!---->
                    <el-row :gutter="20">
                        <el-col :span="12">
                            <el-form-item label="" prop="description">
                                <el-input v-model="passage.description" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <!---->
                    <el-row :gutter="20">
                        <el-col :span="12">
                            <el-form-item label="" prop="type">
                                <el-input v-model="passage.type" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-form-item label="" prop="content">
                        <mavon-editor v-model="passage.content"></mavon-editor>
                    </el-form-item>
                    <!---->
                    <el-form-item>
                        <el-button type="primary" @click="submitForm"></el-button>
                        <el-button @click="resetForm"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-card>
    </div>
</template>

<script>
export default {
    name: "WriteBlog",
    data() {
        return {
            passage: {
                id: '',
                title: '',
                description: '',
                content: '',
                type: '',
            },
            passageRules: {
                title: [
                    {required: true, message: '', trigger: 'blur'},
                    {min: 2, max: 45, message: ' 3  45 ', trigger: 'blur'}
                ],
                description: [
                    {required: true, message: '', trigger: 'blur'},
                    {max: 200, message: '200', trigger: 'blur'}
                ],
                content: [
                    {required: true, message: '', trigger: 'blur'}
                ],
                type: [
                    {required: true, message: '', trigger: 'blur'},
                    {max: 20, message: '20', trigger: 'blur'}
                ]
            }
        }
    },
    methods: {
        submitForm() {
            this.$refs['passage'].validate((valid) => {
                if (valid) {
                    console.log(this.passage)
                    this.$http.post("/passage/add", this.passage).then((response) => {
                        if (response.data.error !== null){
                            this.$message.error(response.data.error)
                            return
                        }
                        this.$message.success("!")
                        this.$router.push("/myBlog")
                    })
                } else {
                    console.log("error")
                }
            })
        },
        resetForm() {
            this.$refs["passage"].resetFields();
        }
    },
    created() {
        if (this.$store.getters.getUser === null || this.$store.getters.getUser === ''){
            this.$message.error("")
            this.$router.push("/login")
        }
    }
}
</script>

<style scoped>
.m-content {
    width: 100%;
}
</style>