<template>
    <div>
        <el-table
                :data="tableData"
                stripe
                style="width: 100%">
            <el-table-column
                    type="index"
                    width="50">
            </el-table-column>
            <el-table-column
                    prop="registerTime"
                    label=""
                    width="250">
            </el-table-column>
            <el-table-column
                    prop="username"
                    label=""
                    width="250">
            </el-table-column>
            <el-table-column
                    prop="type"
                    label=""
                    width="250">
            </el-table-column>
            <el-table-column
                    prop="lastLogin"
                    label=""
                    width="250">
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">
                    </el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[10, 20, 50, 100]"
                :page-size="10"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
        </el-pagination>
        <el-dialog
                title=""
                :visible.sync="dialogEdit"
                width="30%"
                :before-close="handleClose">
            <!--htmlv-bind-->
            <el-form ref="form" :model="formEdit" label-width="80px">
                <el-form-item label="">
                    <el-input v-model="formEdit.username" :placeholder="formEdit.type" disabled></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="formEdit.type" :placeholder="formEdit.type">
                        <el-option label="" value="admin"></el-option>
                        <el-option label="" value="user"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button @click="dialogEdit = false"> </el-button>
                    <el-button type="primary" @click="handleUpdate"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>

</template>

<script>

import axios from "axios";

export default {
    data() {
        return {
            dialogEdit: false,
            tableData: [],
            formEdit: {
                id: '',
                username: '',
                type: ''
            },
            currentPage: 3,
            total: 120
        }
    },
    created() {
        this.selectAll(1)
    },
    methods: {
        selectAll(page) {
            axios.get("/user/all/" + page).then((response) => {
                this.tableData = response.data.data
            })
            axios.get("/user/size").then((response) => {
                this.total = response.data.data
            })
        },
        handleDelete(index, row) {
            console.log("index = " + index)
            console.log(row)
            this.$confirm("?", "", {
                type: "info",
            }).then(() => {
                axios.delete("/user/" + row.id).then((response) => {
                    console.log(response)
                    if (response.data.error === null) {
                        this.$message.success("!");
                        this.selectAll(1);
                    } else {
                        this.$message.error("!");
                    }
                })
            }).catch(() => {
                this.$message.info("");
            })

        },
        handleEdit(index, row) {
            this.formEdit.id = row.id
            this.formEdit.username = row.username
            this.formEdit.type = row.type
            this.dialogEdit = true
        },
        handleClose() {
            this.dialogEdit = false
        },
        handleUpdate() {
            this.$confirm("?", "", {
                type: "info",
            }).then(() => {
                axios.post("/user/updateType", this.formEdit).then((response) => {
                    console.log(response)
                    if (response.data.error === null) {
                        this.$message.success("!");
                        this.selectAll(1);
                        this.dialogEdit = false
                    } else {
                        this.$message.error("!");
                    }
                })
            }).catch(() => {
                this.$message.info("");
            })
        },
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.selectAll(val)
        }
    }
}
</script>